.s5asx20 {
  position: relative;
}
.s5asx20::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--text-normal);
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  transform: scaleX(1);
  transform-origin: left;
  transition: transform 0.5s ease;
}